.ui-toast__wrapper {
    position: fixed;
    height: 100vh;
    width: 100vw;
    top: 0;
    left: 0;
    z-index: 0;
    pointer-events: none;
}

.ui-toast__column {
    width: 400px;
    position: absolute;
    padding: 32px;
    display: flex;
    flex-direction: column;
    top: 0;
    pointer-events: all;
    z-index: 99999;
}

.ui-toast__column > *:not(:last-child) {
    margin-bottom: 16px;
}

.ui-toast__wrapper.position__top-right .ui-toast__column{
    right: 0;
}

.ui-toast__wrapper.position__top-left .ui-toast__column {
    left: 0;
}

.ui-toast__wrapper.position__bottom-left .ui-toast__column {
    left: 0;
    justify-content: flex-end;
    bottom: 0;
    top: auto;
}

.ui-toast__wrapper.position__bottom-right .ui-toast__column {
    right: 0;
    justify-content: flex-end;
    bottom: 0;
    top: auto;
}