.ui-text-area__textarea {
	border-radius: 6px;
	background: #ffffff;
	border: 1px solid #ced6de;
	box-shadow: 0px 1px 2px rgba(63, 63, 68, 0.1);
	padding: 12px 12px 24px 12px;
	font-family: "Circular Std", sans-serif;
	font-weight: 400;
	font-size: 16px;
	line-height: 16px;
	color: #b8c4ce;
}

.ui-text-area__textarea:hover:not(:disabled):not([disabled]):not(.has-error) {
	border-color: #0db9e9;
}

.ui-text-area__textarea:focus:not(:disabled):not([disabled]) {
	border-color: #0db9e9;
	box-shadow: 0px 0px 0px 3px rgba(67, 210, 250, 0.25);
	outline: none;
}

.ui-text-area__textarea:disabled {
	background: rgba(245, 248, 250, 0.5);
	color: #e1e7ec;
    cursor: not-allowed;
}

.ui-text-area::placeholder:disabled {
	color: #e1e7ec;
}

.ui-text-area__label {
	margin-bottom: 8px;
	font-size: 14px;
	line-height: 16px;
	color: #212934;
}

.ui-text-area::placeholder {
	color: #b8c4ce;
}

.ui-text-area__error {
	display: flex;
	align-items: center;
    color: #D62F4B;
}

.ui-text-area__textarea.has-error {
	background: #fff0f2;
	border-color: #d62f4b;
}

.ui-text-area__error-text{
    margin: 0;
}

.ui-text-area__error-icon{
    height: 12px;
    width: 12px;
    margin-right: 5px;
}