.ui-modal {
  background: rgba(0, 0, 0, 0.35);
    /* position: fixed; */
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    padding: 24px;
    overflow-y: scroll;
}

.ui-modal.visibility__visible {
  display: flex;
}

.ui-modal .ui-modal__content {
  background: white;
  min-width: 315px;
  max-width: calc(100vw - 60px);
}

.ui-modal .ui-modal__heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px;
  background: white;
}

.ui-modal .ui-modal__body {
  padding: 24px;
}

.ui-modal.greyContent .ui-modal__content {
  background: #f5f8fa;
}

.root-portal {
  position: fixed;
  z-index: 999;
  top: 0;
  width: 100%;
  left: 0;
}
